<template>
  <h1>Study ukulele</h1>
  <div class="container">
    <div>
      <div>
        <button @click="selectChord('mayor')">Chord Mayor</button>
        <button @click="selectChord('menor')">Chord Menor</button>
        <button @click="selectChord('all')">ALL Chord</button>
      </div>
      <div>
        <button @click="startRamdom">startRamdom</button>
        <button @click="startNormal">startNomal</button>
      </div>
    </div>

    <label>
      Change (s)
      <input type="number" v-model="timer.change">
    </label>
    <label>
      Render (s)
      <input type="number" v-model="timer.render">
    </label>




    <h2 v-show="render.title">{{chord.name}}</h2>
    <visual-chord :chord="chord" v-show="render.chord"></visual-chord>
  </div>
</template>

<script>
import VisualChord from './components/VisualChord.vue'

import {chordsMayor, chordsMenor} from './chords.js';

export default {
  name: 'App',
  components: {
    VisualChord
  },
  data() {
    return {
      chords: [],
      chord: {},
      ramdon: Number,
      startType: '',
      render: {
        title: false,
        chord: false,
      },
      number: -1,
      timer: {
        change: 10,
        render: 3, 
      }
    }
  },
  watch: {
    chord(){
      this.render = {title: true, chord: false,}
      setTimeout(()=>{
        switch (true) {
          case this.startType ===  'ramdom':
              this.startRamdom();
            break;
          case this.startType ===  'normal':
              this.startNormal();
            break;
          default:
            break;
        }
      }, this.timerMS.change)
      setTimeout(()=> {
        this.render.chord = true;
      }, this.timerMS.render)
    }
  },
  computed:{
    timerMS() {
      return {
        change: Number(this.timer.change) * 1000,
        render: Number(this.timer.render) * 1000,
      }
    }
  },

  methods: {
    selectChord(type) {
      switch (true) {
        case type === 'mayor':
            this.chords =  chordsMayor;
          break;
        case type === 'menor':
            this.chords =  chordsMenor;
          break;
        case type === 'all':
            this.chords =  [...chordsMayor, ...chordsMenor];
          break;
        default:
          break;
      }
    },
    startRamdom(){
      this.ramdomChord();
      this.chord =  this.chords[this.ramdon]
      this.startType = 'ramdom'
    },

    startNormal() {
      this.startType = 'normal';

      if(this.number < this.chords.length) {
        this.number += 1; 
      } else {
        this.number = 0
      }
      this.chord =  this.chords[this.number]
    },

    ramdomChord(){
      const i = this.chords.length - 1;
      const ramdomChord =  Math.floor((Math.random() * (i - 0 + 1)))
      if (this.ramdon === ramdomChord) {
        this.ramdomChord();
      } else {
        this.ramdon =  ramdomChord;
      }
    },
  }


}
</script>

